.cart-item {
    display: flex;
    align-items: center;
    margin-bottom: 30px;
    justify-content: space-between;

    &__main {
        display: flex;
        align-items: center;
    }

    &__img {
        height: 200px;
        margin-right: 50px;
    }

    &__details {
        display: flex;
        align-items: center;
    }
    &__price {
        margin-right: 80px;
    }
    &__size {
        margin-right: 80px;
    }
}